export default class Utils {

    // TOOD: improve
    static isValidUrl(imageUrl) {
        return imageUrl.indexOf('http') != -1;
    }

    static addParamToUrl(url = '', paramName, paramValue) {
        if (!url) return url;
        url += (url.includes('?') ? '&' : '?') + `${paramName}=${paramValue}`;
        return url;
    }



}