import React, { useState } from 'react'
import styled, { keyframes } from 'styled-components'
import { css } from 'styled-components'
import { getThisYearSecondsLeft } from '../../utils/dateUtis'
import CountDownTimer from '../countDownTimer/countDownTimer'
import Fireworks from '../fireworks/fireworks'
import Ptt99931Qoute from '../ptt99931Quote/ptt99931Quote'
import '../countDownTimer/style.css';
import './style.css';

export default function CountDown() {
    const [isNewYear, setIsNewYear] = useState(getThisYearSecondsLeft() <= 0);
    const [isQuoteShowingFinsihed, setIsQuoteShowingFinsihed] = useState(false);

    return (
        <div style={{
            width: '100%',
            height: '100%',
        }}>

            {isNewYear && <Fireworks></Fireworks>}

            {isNewYear && <div style={{
                left: isQuoteShowingFinsihed ? null : '50%',
                transform: isQuoteShowingFinsihed ? null : 'translate(-50%, -50%)',
                top: isQuoteShowingFinsihed ? null : '55%',
                position: 'absolute',
                fontSize: '0.8rem',
                width: '100%',
                textAlign: 'center'
            }}>
                <Ptt99931Qoute onQuoteShowingFinsihed={() => setIsQuoteShowingFinsihed(true)}></Ptt99931Qoute>
            </div>}

            {!isNewYear && <div style={{
                position: 'absolute',
                left: '50%',
                transform: 'translate(-50%, -50%)',
                top: '50%',
                textAlign: 'center',
            }}>
                <div className='title'>Countdown mo con</div>

                <img src='https://i.ibb.co/61Ckbsr/05cc26e83ac0e19eb8d1-removebg-preview-3-1.png'
                    width={200}
                    height={200}
                    color='white'
                    style={{
                        borderRadius: 100,
                        margin: '16px 0px 8px 0px'
                    }}
                />

                <CountDownTimer backgroundColor='transparent' onCountFinished={() => setIsNewYear(true)}></CountDownTimer>

                {/* <img src='https://i.ibb.co/QQFsFxH/down-arrow-gif.webp' */}
                <img src='https://i.ibb.co/ZcptPjX/arrow-down-white.webp'
                    width={36}
                    height={36}
                    color='white'
                    style={{
                        marginTop: 12,
                        marginBottom: 12,
                    }}
                />

                <h1 className='title-new-year' style={{
                    textAlign: 'center'
                }}>2023</h1>

            </div>}
        </div>
    )
}

