import React, { useEffect, useState } from 'react'
import { getThisYearSecondsLeft } from '../../utils/dateUtis';
import Div from '../container/div';
import HorizontalDiv from '../container/horizontalDiv';
import VerticalDiv from '../container/verticalDiv';
import './style.css';

const secondPerMinute = 60;
const secondPerHours = 60 * secondPerMinute;
const secondPerDay = 24 * secondPerHours;

const TitleValue = ({ title, value }) => {
    return <VerticalDiv
        style={{
            padding: 32,
            alignItems: 'center',
        }}>
        <Div className={'time'} style={{ marginBottom: 8, color: 'white' }}>{value}</Div>
        <Div className={'time-name'} style={{ color: 'white' }}>{title}</Div>
        {/* <Div style={{ marginBottom: 8, color: 'white', fontSize: '3vw' }}>{value}</Div>
        <Div style={{ color: 'white', fontSize: '1vw', }}>{title}</Div> */}

    </VerticalDiv>;
}

export default function CountDownTimer({ backgroundColor = 'black', onCountFinished }) {
    const [thisYearSecondsLeft, setThisYearSecondsLeft] = useState(getThisYearSecondsLeft());

    useEffect(setThisYearSecondsLeftEverySec, []);

    function getDaysLeft() {
        return ~~(thisYearSecondsLeft / secondPerDay);
    }


    function getHoursLeft() {
        return ~~((thisYearSecondsLeft % secondPerDay) / secondPerHours);
    }

    function getMinutesLeft() {
        return ~~((thisYearSecondsLeft % secondPerHours) / secondPerMinute);
    }


    function getSecondsLeft() {
        return thisYearSecondsLeft % secondPerMinute;
    }

    function setThisYearSecondsLeftEverySec() {
        const secondsLeft = getThisYearSecondsLeft();
        setThisYearSecondsLeft(secondsLeft);
        if (secondsLeft <= 0) {
            onCountFinished();
            return;
        }
        setTimeout(() => {
            setThisYearSecondsLeft(secondsLeft);
            setThisYearSecondsLeftEverySec();
        }, 1000);

    }

    return (
        <HorizontalDiv style={{
            justifyContent: 'center',
            borderRadius: 8,
            backgroundColor: backgroundColor,
            userSelect: 'none',
            padding: '4px 24px 4px 24px',
        }}>
            <TitleValue title={'GI'} value={getHoursLeft()}></TitleValue>
            <TitleValue title={'PHT'} value={getMinutesLeft()}></TitleValue>
            <TitleValue title={'GIY'} value={getSecondsLeft()}></TitleValue>
        </HorizontalDiv>

    )
}
