import React from 'react';
import PropTypes from 'prop-types';

const props = {
    style: PropTypes.oneOfType([PropTypes.object, PropTypes.array]), // Style prop type
}
const defaultProps = {
    style: {}
}

const Div = function ({ style, children, onClick, className }) {
    return (
        <div style={style} onClick={onClick} className={className}>{children}</div>
    )
}

Div.props = props;
Div.defaultProps = defaultProps;

export default Div;
