import './style.css';
import ReactDOM from 'react-dom';
import { useState } from 'react';
import styled from 'styled-components';

const Container = styled.div`
    z-index: 1051;
    background: white;
    position: relative;
    margin: 1.75rem auto;
    border-radius: 3px;
    max-width: 500px;
    padding: ${props => props.padding};
`

const Modal = ({ isShowing, toggle, children, padding = '2rem', header }) => ReactDOM.createPortal(isShowing
    ? (<>
        <div className="modal-overlay" />
        <div className="modal-wrapper" aria-modal aria-hidden tabIndex={-1} role="dialog" onClick={toggle}>
            <Container padding={padding} onClick={(event) => event.stopPropagation()}>
                <div className="modal-header">
                    {header}
                    <button type="button" className="modal-close-button" data-dismiss="modal" aria-label="Close" onClick={toggle}>
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                {children}
            </Container>
        </div>
    </>) : null, document.body);
export default Modal;

export const useModal = () => {
    const [isShowing, setShowing] = useState(false);

    const toggle = () => {
        setShowing(!isShowing);
    }

    return { isShowing, toggle };
}

