import { render } from '@testing-library/react';
import { forwardRef, useCallback, useLayoutEffect, useState } from 'react';
import PhotoAlbum from 'react-photo-album';
import styled from 'styled-components';
import Constants from '../../constants/constants';
import defaultPhotos, { uniqueDefaultImages } from './photos';
import ReactDOM from 'react-dom';
import Modal, { useModal } from '../modal/modal';
import HorizontalDiv from '../container/horizontalDiv';
import Utils from '../../utils/utils';
import Div from '../container/div';
import VerticalDiv from '../container/verticalDiv';
import { DndContext, DragOverlay, MouseSensor, TouchSensor, useSensor, useSensors } from '@dnd-kit/core';
import { CSS } from '@dnd-kit/utilities';
import { arrayMove, rectSortingStrategy, rectSwappingStrategy, SortableContext, useSortable, verticalListSortingStrategy } from '@dnd-kit/sortable';

function Gallery() {

  const [padding, setPadding] = useState(20);
  const [spacing, setSpacing] = useState(24);
  const [columns, setColumns] = useState(4);
  const [imageUrl, setImageUrl] = useState('');
  const [photos, setPhotos] = useState(defaultPhotos);
  const { isShowing, toggle } = useModal();
  const [isRemoveMode, setIsRemoveMode] = useState(false);
  const [draggedIndex, setDraggedIndex] = useState(null);

  useLayoutEffect(() => {
    const viewPortWidth = window.innerWidth;
    setPadding((viewPortWidth < 768) ? 8 : (viewPortWidth < 900) ? 12 : 24);
    setSpacing((viewPortWidth < 768) ? 12 : (viewPortWidth < 900) ? 16 : 20);
    setColumns((viewPortWidth < 768) ? 1 : (viewPortWidth < 900) ? 3 : 4);
  }, []);


  const onAddClicked = useCallback(() => {
    console.log('clicked');
    toggle();
  });

  const onRemoveClicked = useCallback(() => {
    setIsRemoveMode(!isRemoveMode);
  });

  const onOKAddClicked = useCallback(() => {
    if (!imageUrl || !Utils.isValidUrl(imageUrl)) {
      alert('Image url is not valid!');
      return;
    }

    setPhotos([
      ...photos,
      {
        src: Utils.addParamToUrl(imageUrl, 'randomm', photos.length),
        width: 1600,
        height: 900
      }
    ]);
    toggle();
    window.scrollTo(0, document.body.scrollHeight);
    setImageUrl('');
  });

  const SortablePhoto = (props) => {
    const { layout } = props;
    const {
      transform, transition, listeners, attributes, setNodeRef,
      isDragging,
    } = useSortable({ id: layout.index });
    const sortableProps = { ...attributes, ...listeners };
    return <Photo style={{
      opacity: isDragging ? 0.5 : 1,
      zIndex: isDragging ? 1024 : 0,
      transform: CSS.Transform.toString(transform), transition
    }}
      sortableProps={sortableProps}
      {...props}
      ref={setNodeRef}
    >
    </Photo>
  }

  const Photo = forwardRef((props, ref) => {
    const { alt, style, ...rest } = props.imageProps;
    const { index } = props.layout;
    return <div
      ref={ref}
      style={{
        padding: padding,
        borderRadius: "4px",
        boxShadow:
          24 > 0
            ? Constants.shadowPhoto
            : "none",
        transition: "box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms",
        marginBottom: style.marginBottom,
        position: 'relative',
        ...props.style
      }}
      {...props.sortableProps}
    >
      <img
        key={index}
        alt={alt}
        style={{
          ...style,
          marginBottom: 0,
          userSelect: 'none',
        }}
        {...rest}
      />

      {isRemoveMode && <IconButton style={{
        position: 'absolute',
        right: 0,
        top: 0,
        fontWeight: 'normal',
        fontSize: 18,
        textAlign: 'center',
      }}
        onClick={() => onRemovePhotoClicked(index)}>-</IconButton>
      }
    </div>
  });

  const onRemovePhotoClicked = (index) => {
    console.log('remove', index);
    let partUrl = photos[index].src.substring(0, 10);
    let confirmed = window.confirm(`Remove this photo: ${partUrl}`);
    if (confirmed) {
      photos.splice(index, 1);
      setPhotos([...photos]);
    }
  }

  const onDragStart = useCallback((event) => {
    console.log('Drag start', event);
    setDraggedIndex(event.active.id);
  });

  const onDragEnd = useCallback((event) => {
    setPhotos((photos) => {
      const { over, active } = event;
      if (over && active) {
        const fromIndex = active.id;
        const toIndex = over.id;
        return arrayMove(photos, fromIndex, toIndex);
      }
    });

    setDraggedIndex(null);
  });

  const onDragCancel = useCallback((event) => {
    setDraggedIndex(null);
  });

  const touchSensor = useSensor(TouchSensor);
  const mouseSensor = useSensor(MouseSensor);
  const sensors = useSensors(mouseSensor, touchSensor);

  return <Div>
    <HorizontalDiv style={{
      top: '50%',
      left: -48,
      textAlign: 'left',
      fontWeight: 'bold',
      color: 'rgb(0 0 0 / 48%)',
      position: 'absolute',
      transform: 'translate(0, -50%) rotate(-90deg)',
      display: 'flex',
      alignItems: 'center',
      userSelect: 'none',
    }}>
      <Div style={{
        fontSize: 48,
      }}>
        Gallery
      </Div>
      <IconButton style={{ marginLeft: 16, transform: 'rotate(90deg)', textAlign: 'center', paddingBottom: 28, boxSizing: 'border-box' }}
        onClick={onRemoveClicked}>-</IconButton>
      <IconButton style={{ marginLeft: 16 }} onClick={onAddClicked}>+</IconButton >
    </HorizontalDiv>

    <Div style={{
      padding: '48px 64px 56px 64px',
      boxShadow: Constants.shadowContainer,
      margin: '32px 64px 32px 64px'
    }}>

      <DndContext onDragStart={onDragStart}
        onDragEnd={onDragEnd}
        onDragCancel={onDragEnd}
        sensors={sensors}>
        <SortableContext items={photos.map((_, index) => index)} strategy={rectSwappingStrategy}>
          <PhotoAlbum layout='masonry'
            photos={photos}
            renderPhoto={(props) => (<SortablePhoto {...props}></SortablePhoto>)}
            spacing={spacing}
            columns={columns}

          ></PhotoAlbum>
        </SortableContext>

        {/* <DragOverlay adjustScale={true}>
          {draggedIndex != null && <div style={{ backgroundColor: 'grey' }}>DIU</div>}
        </DragOverlay> */}
      </DndContext>

      <Modal isShowing={isShowing} toggle={toggle}
        header={<h3>Add memory</h3>}>
        <HorizontalDiv>
          <input type="text" value={imageUrl}
            onChange={(event) => setImageUrl(event.target.value)} placeholder='Image URL'
            style={{ flex: 1, paddingLeft: 8, }} />
          <button text={'Add'} onClick={onOKAddClicked}>OK</button>
        </HorizontalDiv>
      </Modal>

    </Div>
  </Div>
};

const IconButton = styled.div(props => ({
  backgroundColor: 'white',
  boxShadow: '1px 2px 2px 2px rgb(0 0 0 / 8%)',
  width: 24,
  height: 24,
  fontSize: 24,
  color: 'black',
  fontWeight: 'bold',
  borderRadius: '50%',
  textAlign: 'center',
  cursor: 'pointer',
  '&:hover': {
    boxShadow: '1px 2px 2px 2px rgb(0 0 0 / 12%)',
  },
  '&:active': {
    backgroundColor: 'rgb(0 0 0 / 2%)',
  },
  ...props.style,
}));

export default Gallery;
